<template>
  <div class="hello">
    <form enctype="multipart/form-data" @submit.stop.prevent="onUpload">
      <img ref="outImage" class="mb-1" width="90" height="50" /><br>
      <input type="file" id="file" ref="file" class="m-1"
        accept="image/png, image/gif, image/jpeg" 
        @change="onFileChange" /><br>
      <!-- <button type="submit" class="mr-1">Upload</button>
      <button @click="OnRemove" class="mr-1">Remove</button> -->
    </form>
  </div>
</template>
<script>
import $ from "jquery";
const base64Encode = data =>
  new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(data);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
  });
export default {
  name: 'HelloWorld',
  props: ['msg','src'],
  mounted(){
    $(this.$refs.outImage).attr("src",this.src);
  },
  methods: {
    onUpload() {
      let file=this.getFile();
      if(file=="") return;
      alert(file.name);
    },
    getFile(){
      let files=this.$refs.file.files;
      let file=( files.length == 0 ) ? "" : files[0];
      return file;
    },
    onFileChange() {
      let file=this.getFile();
      base64Encode(file).then(res=>{
        $(this.$refs.outImage).attr("src",res);
      }) 
    },
    OnRemove(){
      $(this.$refs.outImage).attr("src","");
      $(this.$refs.file).val("");
    }    
  }
}
</script>
