export default "<template>\n  <div class=\"MonHocToan\">\n    <raw-loader :mytxt=\"require('!raw-loader!./CrudWithImage.vue').default\"></raw-loader>\n\n    <b-container fluid>\n      <!-- User Interface controls -->\n      <!-- Main table element -->\n      <form @submit.stop.prevent=\"onSave\">\n        <b-table thead-class=\"myTable\" ref=\"mytable\" :items=\"myProvider\" :fields=\"fields\" :current-page=\"currentPage\"\n          :per-page=\"perPage\" :filter=\"filter\" :filter-included-fields=\"filterOn\" :sort-by.sync=\"sortBy\"\n          :sort-desc.sync=\"sortDesc\" :sort-direction=\"sortDirection\" stacked=\"sm\" show-empty small fixed outlined\n          bordered @filtered=\"onFiltered\">\n          <template v-slot:cell()=\"{ value, item, field: { key } }\">\n            <template v-if=\"sel[pri] != item[pri]\">\n              <template v-if=\"item[pri] != 0\">\n                <template v-if=\"key == pri\">\n                  {{ value }}\n                </template>\n                <template v-if=\"key == 'HinhAnh'\">\n                  <img :src=\"`/api/FileUpload/GetImage/${item[key]}`\" width=\"50\" height=\"30\" />\n                </template>\n                <template v-if=\"key == 'Ten'\">\n                  {{ value }}\n                </template>\n                <template v-if=\"key == 'NgayMua'\">\n                  {{ value }}\n                </template>\n                <template v-if=\"key == 'GiaTien'\">\n                  {{ value }}\n                </template>\n              </template>\n            </template>\n            <template v-if=\"(sel[pri] == item[pri] || item[pri] == 0)\">\n              <template v-if=\"key == 'Id'\">\n                <span>{{ value }}</span>\n              </template>\n              <template v-if=\"key == 'HinhAnh'\">\n                <com-upload-2 ref=\"HinhAnh\" :src=\"`/api/FileUpload/GetImage/${item[key]}`\"></com-upload-2>\n              </template>\n              <template v-if=\"key == 'Ten'\">\n                <b-form-input v-model=\"item[key]\" :name=\"key\" v-validate=\"'required'\" placeholder=\"\"\n                  :class=\"{ 'ko-hop-le': errors.has(key) }\">\n                </b-form-input>\n              </template>\n              <template v-if=\"key == 'NgayMua'\">\n                <b-form-datepicker v-model=\"item[key]\"\n                  :date-format-options=\"{ year: 'numeric', month: 'numeric', day: 'numeric' }\" locale=\"vi\" class=\"mb-2\">\n                </b-form-datepicker>\n              </template>\n              <template v-if=\"key == 'GiaTien'\">\n                <b-form-input v-model=\"item[key]\" :name=\"key\" v-validate=\"'required'\" placeholder=\"\"\n                  :class=\"{ 'text-right':true, 'ko-hop-le': errors.has(key) }\">\n                </b-form-input>\n              </template>\n            </template>\n          </template>\n          <template #cell(cmd)=\"row\">\n            <template v-if=\"row.item[pri] == 0\">\n              <b-button class=\"mr-1\" size=\"sm\" @click=\"onSave(row.item)\">\n                <b-icon-hand-thumbs-up></b-icon-hand-thumbs-up>\n              </b-button>\n              <b-button class=\"mr-1\" size=\"sm\" @click=\"OnCancel(row.item)\">\n                <b-icon-file-x></b-icon-file-x>\n              </b-button>\n            </template>\n            <template v-else>\n              <b-button variant=\"outline\" class=\"mr-1\" v-if=\"sel[pri] != row.item[pri]\" size=\"sm\"\n                @click=\"OnEdit(row.item)\">\n                <b-icon-pencil-square></b-icon-pencil-square>\n              </b-button>\n              <b-button variant=\"outline\" class=\"mr-1\" v-if=\"sel[pri] != row.item[pri]\" size=\"sm\"\n                @click=\"OnDel(row.item)\">\n                <b-icon-trash></b-icon-trash>\n              </b-button>\n              <b-button variant=\"outline\" class=\"mr-1\" v-if=\"sel[pri] == row.item[pri]\" size=\"sm\"\n                @click=\"onSave(row.item)\">\n                <b-icon-hand-thumbs-up></b-icon-hand-thumbs-up>\n              </b-button>\n              <b-button variant=\"outline\" class=\"mr-1\" v-if=\"sel[pri] == row.item[pri]\" size=\"sm\"\n                @click=\"OnCancel(row.item)\">\n                <b-icon-file-x></b-icon-file-x>\n              </b-button>\n            </template>\n          </template>\n\n          <template #bottom-row=\"data\">\n            <td :colspan=\"data.columns\">\n              <b-row>\n                <b-col sm=\"6\" class=\"my-1\">\n                  <b-form-group label=\"Tìm kiếm\" label-for=\"filter-input\" label-cols-sm=\"3\" label-align-sm=\"right\"\n                    label-size=\"sm\" class=\"mb-0\">\n                    <b-input-group size=\"sm\">\n                      <b-form-input id=\"filter-input\" v-model=\"filter\" type=\"search\" placeholder=\"Type to Search\">\n                      </b-form-input>\n\n                      <b-input-group-append>\n                        <b-button :disabled=\"!filter\" @click=\"filter = ''\">Clear</b-button>\n                      </b-input-group-append>\n                    </b-input-group>\n                  </b-form-group>\n                </b-col>\n                <b-col sm=\"6\" class=\"my-1\">\n                  <b-form-group label=\"Số dòng\" label-for=\"per-page-select\" label-cols-sm=\"6\" label-cols-md=\"4\"\n                    label-cols-lg=\"3\" label-align-sm=\"right\" label-size=\"sm\" class=\"mb-0\">\n                    <b-form-select id=\"per-page-select\" v-model=\"perPage\" :options=\"pageOptions\" size=\"sm\">\n                    </b-form-select>\n                  </b-form-group>\n                </b-col>\n\n                <b-col sm=\"6\" class=\"my-1 text-right\">\n                  <span>page : {{ currentPage }} / {{ last_page }} of {{ totalRows }} </span>\n                  <b-button :disabled=\"adding == true || isEditing() == true\" class=\"mr-1 mb-1\" size=\"sm\"\n                    @click=\"OnAdd\">\n                    <b>Add</b></b-button>\n                </b-col>\n                <b-col sm=\"6\" class=\"my-1\">\n                  <b-pagination ref=\"mypage\" v-model=\"currentPage\" :total-rows=\"totalRows\" :per-page=\"perPage\" size=\"sm\"\n                    align=\"fill\" @change=\"onChangePage\" @input=\"onPagingInput\" first-text=\"First\" prev-text=\"Prev\"\n                    next-text=\"Next\" last-text=\"Last\">\n                  </b-pagination>\n                </b-col>\n              </b-row>\n            </td>\n          </template>\n          <template #row-details=\"row\">\n            <b-card>\n              <ul>\n                <li v-for=\"(value, key) in row.item\" :key=\"key\">{{ key }}: {{ value }}</li>\n              </ul>\n            </b-card>\n          </template>\n        </b-table>\n\n      </form>\n      <!-- Info modal -->\n      <b-modal :id=\"infoModal.id\" :title=\"infoModal.title\" ok-only @hide=\"resetInfoModal\">\n        <pre>{{ infoModal.content }}</pre>\n      </b-modal>\n    </b-container>\n  </div>\n</template>\n<script>\n\nimport VeeValidate from \"vee-validate\";\nimport Vue from \"vue\";\nVue.use(VeeValidate, {\n  classes: true,\n  fieldsBagName: 'veeFields'\n});\n\nimport axios from 'axios'\nimport ComUpload2 from \"./ComUpload2.vue\"\nimport moment from \"moment\";\nimport $ from \"jquery\"\n\nexport default {\n  components: {\n    ComUpload2\n  },\n\n  data() {\n    return {\n      items: [\n      ],\n      fields: [\n        {\n          key: 'Id',\n          formatter: (value, key, item) => {\n            return value;\n          },\n          sortable: true,\n          thClass: 'text-center',\n          tdClass: 'text-center align-middle',\n          thStyle: { width: \"10%\" },\n        },\n        {\n          key: 'HinhAnh',\n          label:'Hình',\n          sortable: true,\n          thClass: 'text-center',\n          tdClass: 'align-middle text-center',\n          thStyle: { width: \"20%\" },\n        },\n        {\n          key: 'Ten',\n          label:'Tên thiết bị',\n          sortable: true,\n          thClass: 'text-center',\n          tdClass: 'align-middle',\n          thStyle: { width: \"35%\" },\n        },\n        {\n          key: 'NgayMua',\n          label:'Ngày mua',\n          formatter: (value, key, item) => {\n            return moment(value).format(\"DD/MM/YYYY\");\n          },\n          sortable: true,\n          thClass: 'text-center',\n          tdClass: 'align-middle text-center',\n          thStyle: { width: \"15%\" },\n        },\n        {\n          key: 'GiaTien',\n          label:'Giá tiền',\n          formatter: (value, key, item) => {\n            return parseInt(value).toLocaleString();\n          },\n          sortable: true,\n          thClass: 'text-center',\n          tdClass: 'align-middle text-right',\n          thStyle: { width: \"10%\" },\n        },\n        {\n          key: 'cmd',\n          class: 'text-center',\n          thClass: 'text-center',\n          tdClass: 'align-middle',\n          thStyle: { width: \"10%\" }\n        }\n\n      ],\n      totalRows: 1,\n      currentPage: 1,\n      perPage: 5,\n      pageOptions: [5, 10, 15, { value: 100, text: \"Show a lot\" }],\n      sortBy: '',\n      sortDesc: false,\n      sortDirection: 'asc',\n      filter: null,\n      filterOn: [],\n      infoModal: {\n        id: 'info-modal',\n        title: '',\n        content: ''\n      },\n\n      sel: {},\n      pri: \"Id\",\n      last_page: 0,\n      adding: false\n\n    }\n  },\n\n  watch: {\n    last_page: function (newValue, oldValue) {\n    },\n    currentPage: function (val) {\n    },\n    totalRows: function (newValue, oldValue) {\n    }\n  },\n  computed: {\n  },\n  mounted() {\n  },\n  methods: {\n    onPagingInput() {\n    },\n    myProvider(ctx) {\n      let currentPage = ctx.currentPage;\n      let perPage = ctx.perPage;\n      let sortBy = ctx.sortBy;\n      let sortDesc = ctx.sortDesc;\n      let vm = this;\n      let url = `/api/ThietBi/GetPage2`;\n      url = `${url}?page=${currentPage}&size=${perPage}&sortBy=${sortBy}&sortDesc=${sortDesc}`;\n      if (ctx.filter) {\n        let filter = ctx.filter;\n        url = `${url}&filter=${filter}`;\n      }\n      return axios.get(url)\n        .then(res => {\n          vm.last_page = res.data.last_page;\n          vm.totalRows = res.data.rowCount;\n          if (this.adding == true) {\n            let r = {};\n            r[this.pri] = 0;\n            r[\"Ten\"] = \"\";\n            r[\"HinhAnh\"] = \"no_img.png\";\n            r[\"NgayMua\"] = new Date();\n            r[\"GiaTien\"] = 1000;\n            res.data.data.unshift(r);\n          }\n          return vm.mapData(res.data.data); //res.data.data;\n        })\n    },\n    mapData(data) {\n      let kq = [];\n      data.forEach(obj => {\n        let x = {};\n        Object.keys(obj).forEach(key => {\n          let k = key;\n          x[k] = obj[key];\n        });\n        kq.push(x);\n      })\n      return kq;\n    },\n    onChangePage() {\n      this.sel = {};\n      this.adding = false;\n    },\n    OnAdd() {\n      this.adding = true;\n      this.$refs.mytable.refresh();\n    },\n    OnEdit(item) {\n      if (JSON.stringify(this.sel) != \"{}\" || this.adding == true) {\n        return;\n      }\n      this.sel = JSON.parse(JSON.stringify(item));\n    },\n    onSave(item) {\n      this.$validator.validateAll().then((result) => {\n        if (!result) {\n          this.toast(\"Không hợp lệ\");\n          return;\n        }\n        let r = JSON.parse(JSON.stringify(item));\n        let file = this.$refs.HinhAnh.getFile();\n        var formData = new FormData();\n        formData.append(\"Id\", r.Id);\n        formData.append(\"Ten\", r.Ten);\n        formData.append(\"HinhAnh\", r.HinhAnh);\n        formData.append(\"NgayMua\", r.NgayMua);\n        formData.append(\"GiaTien\", r.GiaTien);\n        formData.append(\"File\", file);\n        let that = this;\n        $.ajax(\n          {\n            url: \"/api/ThietBi/Save3\",\n            data: formData,\n            processData: false,\n            contentType: false,\n            type: \"POST\",\n            success: function (res) {\n              alert(JSON.stringify(res));\n              if (item.pri == 0) {\n              } else {\n                that.sel = {};\n              }\n              that.toast(`${res.msg}`);\n              that.adding = false;\n              that.$refs.mytable.refresh();\n            }\n          }\n        );\n      });\n    },\n    OnCancel(item) {\n      if (item[this.pri] != 0) {\n        this.doCancel(item);\n        this.sel = {};\n      } else {\n        this.adding = false;\n        this.$refs.mytable.refresh();\n      }\n    },\n    OnDel(item) {\n      this.Del(item[this.pri]).then(res => {\n        //alert(JSON.stringify(res));\n        //{\"data\":{\"kq\":\"ok\",\"msg\":\"Delete thiết bị ok\"},\"status\":200,\"statusText\":\"OK\",\"headers\":{\"content-length\":\"42\",\"con\n        if( res.data.kq != 'ok' ){\n          return;\n        }\n        this.toast(`${res.data.msg}`)        \n        let lastPage = this.getLastPage(this.totalRows - 1);\n        if (lastPage < this.currentPage) this.currentPage = lastPage;\n        this.$refs.mytable.refresh();\n      })\n    },\n    delRow(item) {\n      let id = item.id;\n      let index = this.items.findIndex(x => x.id == id);\n      this.items.splice(index, 1);\n    },\n    isEditing() {\n      return JSON.stringify(this.sel) != \"{}\"\n    },\n    isAdded() {\n      let kq = false;\n      this.items.forEach(x => {\n        if (x.id == 0) kq = true;\n      })\n      return kq;\n    },\n    doCancel(item) {\n      for (let key in item) {\n        if (item[key] != this.sel[key]) {\n          item[key] = this.sel[key]\n        }\n      }\n    },\n    getLastPage(count) {\n      let pageSize = this.perPage;//this.tabulator.getPageSize();\n      let du = count % pageSize;\n      return du == 0 ? count / pageSize : (count - du) / pageSize + 1;\n    },\n    gotoPage(page) {\n      this.currentPage = page;\n      this.$refs.mytable.refresh();\n    },\n\n    toast(msg, variant = null) {\n      this.$bvToast.toast(msg, {\n        title: `Information`,\n        variant: variant,\n        solid: true,\n      });\n    },\n\n    async Save(r) {\n      return await axios.post(`/api/Ban/Save`, r).catch((err) => {\n        console.log(err);\n      });\n    },\n    async Del(id) {\n      return await axios\n        .get(`/api/ThietBi/Delete?id=${id}`)\n        .catch((err) => {\n          console.log(err);\n        });\n    },\n    async getById(id) {\n      return await axios.get(`/api/Ban/GetById?id=${id}`).catch((err) => {\n        console.log(err);\n      });\n    },\n    async GetCombo() {\n      return await axios.get(`/api/Ban/GetCombo`).catch((err) => {\n        console.log(err);\n      });\n    },\n    info(item, index, button) {\n      this.infoModal.title = `Row index: ${index}`\n      this.infoModal.content = JSON.stringify(item, null, 2)\n      this.$root.$emit('bv::show::modal', this.infoModal.id, button)\n    },\n    resetInfoModal() {\n      this.infoModal.title = ''\n      this.infoModal.content = ''\n    },\n    onFiltered(filteredItems) {\n      // Trigger pagination to update the number of buttons/pages due to filtering\n      this.totalRows = filteredItems.length\n      this.currentPage = 1\n    }\n  }\n\n}\n</script>\n<style lang=\"scss\">\n@import \"@/assets/scss/myStyle.scss\";\n</style>\n\n";